:root {
  --color-primary1: #092424;
  --color-primary2: #18675d;
  --color-secondary: #ffc367;
  --color-text-title: #ffd597;
  --color-card: #2b2b2b4b;
  --color-text: #fafafa;
  --color-body: #121212;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
#root {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
@font-face {
  font-family: "SVN-Book Antiqua";
  src: url("./fonts/SVN-Book Antiqua.ttf") format("truetype");
}
@font-face {
  font-family: "SVN-Book Avo";
  src: url("./fonts/SVN-Avo.ttf") format("truetype");
}

a {
  text-decoration: none;
  color: var(--color-text);
}

a:hover {
  color: var(--color-text-title);
}

body {
  color: var(--color-text);
  font-family: "SVN-Book Avo";
  background-color: var(--color-body);
}

.fs__smallest {
  font-size: 12px;
}

.fs__details {
  font-size: 14px;
}

.fs__text {
  font-size: 18px;
}

.fs__title {
  font-size: 28px;
  font-family: "SVN-Book Antiqua", sans-serif;
  color: var(--color-text-title);
}

.fs__title--main {
  font-size: 42px;
  color: var(--color-text-title);
  font-family: "SVN-Book Antiqua", sans-serif;
}

.button-secondary {
  background-color: var(--color-secondary);
  color: var(--color-text);
}

.button-secondary:hover {
  background-color: #ffc267d7;
  color: black;
}

.button-secondary--outline {
  color: var(--color-text);
  -webkit-box-shadow: 0 0 0 2px var(--color-secondary) inset;
  box-shadow: 0 0 0 2px var(--color-secondary) inset;
}

.button-secondary--outline:hover {
  background-color: var(--color-secondary);
  color: black;
}
.row {
  margin-left: 0;
  margin-right: 0;
}
.bg__custom--1 {
  background-image: url(./images/breadcrumbs-bg.jpg);
  background-size: cover;
  position: relative;
  z-index: -1;
}
.bg__custom--1::before {
  content: "";
  width: 100%;
  height: 70px;
  transform: rotate(180deg);
  background-image: url(./images/overlay-slider.webp);
  background-size: contain;
  position: absolute;
  z-index: 0;
  top: 0;
}
.bg__custom--1::after {
  content: "";
  width: 100%;
  height: 70px;
  background-image: url(./images/overlay-slider.webp);
  background-size: contain;
  position: absolute;
  z-index: 0;
  bottom: 0;
}
